// global

// Wavy Newsletter Section

.wavy-newsletter {
margin: 4rem auto;
padding: 5rem 0;
color: $white;
line-height: 1.5;

.wavy-newsletter-title {
  font-weight: 300;
  width: 80%;
  font-size: 2.5rem;

  @include desktop {
    font-weight: 600;
    font-size: 3.5rem;
  }
}

.wavy-newsletter-form {
  margin-bottom: 6rem;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 80%;
  flex-direction: column;

  @include desktop {
    align-items: center;
    justify-content: space-evenly;
    width: 70%;
    flex-direction: row;
  }

  input {
    width: 100%;
    padding: 1rem;
    margin: 0.8rem 0;
    font-size: 1rem;

    @include desktop {
      width: 30%;
    }
  }

  button {
    background: $white;
    color: $light_green;
    padding: .8rem 4rem;
    align-self: inherit;
    margin-top: 0.75rem;

    @include desktop {
      padding: 13px 70px;
      margin: 0.8rem 0;
    }
  }
}
}
